import { router } from "./router";

import * as NProgress from "nprogress";
import "nprogress/nprogress.css";

/*  */
router.beforeEach((to, from, next) => {
  NProgress.start();

  /* login */
  if (to.path === "/") {
    next(`/home`);
  } else {
    next();
  }
});

router.afterEach(() => {
  NProgress.done(true);
});
