<template>
    <div>
      <input v-model="url" placeholder="URL" />
      <input v-model="divClass" placeholder="divclass" />
      <button @click="downloadImages"></button>
      <div v-if="status">{{ status }}</div>
    </div>
</template>

<script setup>
import { ref } from 'vue';
import axios from 'axios';
import axiosJsonp from 'axios-jsonp';
import JSZip from 'jszip';
import { saveAs } from 'file-saver';

const url = ref('');
const divClass = ref('');
const status = ref('');

async function downloadImages() {
    status.value = '...';

    try {
        // 
        const response = await axios.get(`http://localhost/everia?url=${encodeURIComponent(url.value)}`);
        const parser = new DOMParser();
        const doc = parser.parseFromString(response.data, 'text/html');
        const targetDiv = doc.querySelector(`.${divClass.value}`);

        if (!targetDiv) {
            status.value = 'div';
            return;
        }

        const imgElements = targetDiv.querySelectorAll('img');

        if (imgElements.length === 0) {
            status.value = 'div';
            return;
        }

        status.value = ` ${imgElements.length} ...`;

        const zip = new JSZip();
        const imagePromises = Array.from(imgElements).map(async (img, index) => {
            const imgUrl = img.src;
            const imgResponse = await axios.get(imgUrl, { responseType: 'arraybuffer' });
            const filename = `image_${index + 1}.jpg`;
            zip.file(filename, imgResponse.data);
        });

        await Promise.all(imagePromises);

        status.value = 'ZIP...';
        const content = await zip.generateAsync({ type: 'blob' });
        saveAs(content, 'images.zip');

        status.value = '';
    } catch (error) {
        status.value = '' + error.message;
    }
}
</script>

<style lang="scss" scoped>
div {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;

    input, button {
        margin: 10px 0;
        padding: 5px 10px;
    }
}
</style>