/**
 * 
 * @param method 
 * @param status 
 * @param message 
 * @returns
 */
export default (method, status, message) => {
  if (status) {
    if (status >= 200 && status < 300) {
      if (message) return message;
      switch (method) {
        case "GET":
          return "";
        case "POST":
          return "/";
        case "DELETE":
          return "";
        case "PATCH":
          return "";
        default:
          return "";
      }
    }
    if (status >= 400 && status < 500) {
      if (message) return `${status}: ${message}`;
      switch (status) {
        case 401:
          return "401: ";
        case 403:
          return "403: ";
        case 404:
          return "404: ";
        default:
          return "";
      }
    }
    if (status >= 500 && status < 600) {
      switch (status) {
        case 502:
          return "502: ";
        case 503:
          return "503: ";
        case 504:
          return "504: ";
        default:
          return "";
      }
    }
  }
  if (message?.startsWith("timeout")) return "";
  if (message?.startsWith("Network Error"))
    return "";
  return "";
};
